import gql from "graphql-tag";
import { PayoutFragment } from "./payouts-fragments";


export const CREATE_PAYOUTS = gql`
mutation(
  $month: Int!,
  $year: Int!,
  $query: ConnectionQuery!
) {
  createPayouts(month: $month, year: $year) {

    ...PayoutFragment

    payoutItemsConnection(query: $query) {
      pageInfo {
        isLastPage
        totalPages
      }
      totalCount
      totalAmount
      edges {
        cursor
        node {
          id
          orderItemId
          amount
      		payoutStatus
          transaction {
            id
            orderId
            taxes
            subtotal
            sellerPayment
            platformFee
            paymentProcessingFee
          }
        }
      }
    }
  }
}
${PayoutFragment}
`;

export const APPROVE_PAYOUTS = gql`
mutation($payoutIds: [String]!) {
	approvePayouts(payoutIds: $payoutIds) {

    approvedPayouts {
      ...PayoutFragment
      payoutItemsConnection(query:{
        cursor: null,
        count: 5,
        sortAscending: false,
        pageBackwards: false
      }) {
        edges {
          cursor
          node {
            id
            payoutStatus
          }
        }
      }
    }
    payoutsAlreadyApproved {
      ...PayoutFragment
      payoutItemsConnection(query:{
        cursor: null,
        count: 5,
        sortAscending: false,
        pageBackwards: false
      }) {
        edges {
          cursor
          node {
            id
            payoutStatus
          }
        }
      }
    }
  }
}
${PayoutFragment}
`;