import gql from "graphql-tag";
import {
  PayoutItemFragment,
  TransactionFragment,
} from "./payouts-fragments";



export const GET_PAYOUTS_IN_PERIOD_ADMIN = gql`
query(
  $month: Int!,
  $year: Int!,
  $query: ConnectionQuery!
) {
  getPayoutsInPeriodAdmin(
    month: $month,
    year: $year,
    query: $query
  ) {
    totalCount
    totalAmount
    pageInfo {
      endCursor
      totalPages
      isLastPage
    }
    edges {
      cursor
      node {
        id
        payeeId
        payeeType
        amount
        payoutDate
        payoutEmail
        payoutStatus
        startPeriod
        endPeriod
        approvedByAdmins {
          id
          email
          firstName
        }
        payoutItemsConnection(query: {
          sortAscending: false,
          pageBackwards: false,
          count: 5,
          cursor: null
        }) {
          pageInfo {
            endCursor
            totalPages
            isLastPage
          }
          totalCount
          edges {
            cursor
            node {
              id
              txnId
              payeeId
              payeeType
              amount
              createdAt
              currency
              payoutStatus
              payoutId
              orderItemId
              orderItem {
                id
                orderId
                product {
                  name
                  store {
                    id
                    name
                    website
                  }
                }
              }
              transaction {
                id
                orderId
                taxes
                subtotal
                sellerPayment
                platformFee
                paymentProcessingFee
              }
            }
          }
        }
      }
    }
  }
}
`;
