// Styles
import { withStyles, createStyles, WithStyles, Theme } from "@material-ui/core/styles";
import { Colors } from "layout/AppTheme";
// MUI
import Typography from "@material-ui/core/Typography";
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Button from '@material-ui/core/Button';

import Layout from "layout";

import {
  PayoutItem,
  PayoutStatus,
  Connection, ConnectionQuery, Edge,
  StoreSales,
  Payout,
  ID,
  Transaction,
} from "typings";
// Graphql
import { useQuery, useMutation, useApolloClient } from "@apollo/react-hooks";
import {
  GET_PAYOUTS_IN_PERIOD_ADMIN,
} from "queries/payouts-queries";
import {
  CREATE_PAYOUTS,
  APPROVE_PAYOUTS,
} from "queries/payouts-mutations";
import Loading from 'components/Loading';
import SnackBarA from "components/Snackbars/SnackbarA";
// Components
import PeriodPicker from "./PeriodPicker";
import PayoutItemsTable from "./PayoutItemsTable";
import TransactionsTable from "./TransactionsTable";
import PayoutsTable from "./PayoutsTable";
// formatters
import dayjs from 'dayjs';
import currency from "currency.js";
import NoSSR from 'react-no-ssr';





const PayoutApprovals: FunctionComponent<ReactProps> = (props) => {

  const { classes } = props;
  const aClient = useApolloClient();
  const c = (s) => currency(s/100, { formatWithSymbol: true }).format()
  // const asTime = (d: Date) => dayjs(d).format("YYYY-MM-DD HH:mm:ss")
  const asTime = (d: Date) => dayjs(d).format("YYYY-MM-DD")

  const d = new Date();
  // get current month + year
  const [month, setMonth] = React.useState<any>(d.getMonth() + 1);
  const [year, setYear] = React.useState<any>(d.getUTCFullYear());

  const [isLoading, setIsLoading] = React.useState(false);
  const [hasQueryResponded, setGqlResponded] = React.useState(false);

  const [selectApprovePayouts, setSelectApprovePayouts] = React.useState([]);
  const [payoutCount, setPayoutCount] = React.useState(5);

  const [createPayouts, createPayoutsResponse] =
  useMutation<MutationData, MutationVar>(
    CREATE_PAYOUTS, {
    variables: {
      month: month,
      year: year,
      query: {
        sortAscending: false,
        pageBackwards: false,
        count: payoutCount,
        cursor: null
      }
    },
    onError: (err) => {
      console.log(err)
      setGqlResponded(true)
    },
    update: (cache, { data }: { data: MutationData }) => {
      setGqlResponded(true)
      try {
        let newData = aClient.query({
          query: GET_PAYOUTS_IN_PERIOD_ADMIN,
          variables: {
            month: month,
            year: year,
            query: {
              sortAscending: false,
              pageBackwards: false,
              count: payoutCount,
              cursor: null
            }
          }
        });
        cache.writeQuery({
          query: GET_PAYOUTS_IN_PERIOD_ADMIN,
          data: newData
        });
      } catch (error) {
        console.log(error);
      }
    }
  })

  const [approvePayouts, approvePayoutsResponse] =
  useMutation<MutationData2, MutationVar2>(
    APPROVE_PAYOUTS, {
    variables: {
      payoutIds: [],
      query: {
        sortAscending: false,
        pageBackwards: false,
        count: payoutCount,
        cursor: null
      }
    },
    onError: (err) => {
      console.log(err)
      setGqlResponded(true)
    },
    update: (cache, { data }: { data: MutationData2 }) => {
      setGqlResponded(true)
      try {
        if (data.approvePayouts.payoutsAlreadyApproved.length > 0) {
          console.log(
            'Already approved payouts: ',
            data.approvePayouts.payoutsAlreadyApproved
          );
          alert("Already approved: " + JSON.stringify(data))
        }

        if (data.approvePayouts.approvedPayouts.length > 0) {
          console.log(
            'Newly approved payouts: ',
            data.approvePayouts.approvedPayouts
          );
          alert("newly approved: " + JSON.stringify(data))
        }
      } catch (error) {
        console.log(error);
      }
    }
  })

  return (
    <div className={classes.root}>
      <div className={classNames(classes.spaceBetween)}>

        <div className={classNames(classes.flexRow, classes.spaceBetween)}>
          <Typography color={"primary"} variant="h4" gutterBottom>
            Payout Approvals Admin
          </Typography>
        </div>

        <NoSSR>
          <PayoutItemsTable
            month={month}
            year={year}
          />
        </NoSSR>
        <br/>

        <NoSSR>
          <TransactionsTable
            month={month}
            year={year}
          />
        </NoSSR>
        <br/>

        <PeriodPicker
          month={month}
          setMonth={setMonth}
          year={year}
          setYear={setYear}
        >
          <Button
            onClick={async() => {
              setIsLoading(true)
              await createPayouts({
              })
              setIsLoading(false)
            }}
            variant="outlined"
            color="secondary"
            className={classes.createPayoutsButton}
          >
            Create Payouts
          </Button>

          <Button
            onClick={async() => {
              setIsLoading(true)
              const payoutIds = selectApprovePayouts.map(({ node }) => node.id);
              console.log("dispatching payouts for: ", payoutIds)
              await approvePayouts({
                variables: {
                  payoutIds: payoutIds
                }
              })
              setIsLoading(false)
            }}
            variant="outlined"
            color="primary"
            className={classes.createPayoutsButton}
          >
            Approve Payouts
          </Button>
        </PeriodPicker>

        <NoSSR>
          <PayoutsTable
            month={month}
            year={year}
            setSelectApprovePayouts={setSelectApprovePayouts}
          />
        </NoSSR>

        <Loading fixed loading={isLoading} delay={'200ms'}/>

        <SnackBarA
          open={hasQueryResponded && !!createPayoutsResponse.data}
          closeSnackbar={() => setGqlResponded(false)}
          message={
            `Created Payouts: ${
              JSON.stringify(option(createPayoutsResponse).data.createPayouts())
            }`
          }
          variant={"success"}
        />
        <SnackBarA
          open={hasQueryResponded && !!approvePayoutsResponse.data}
          closeSnackbar={() => setGqlResponded(false)}
          message={
            `Already Approved Payouts:
            ${JSON.stringify(option(approvePayoutsResponse).data.approvePayouts.payoutsAlreadyApproved())}
            Approved Payouts:
            ${JSON.stringify(option(approvePayoutsResponse).data.approvePayouts.approvedPayouts())}
            `
          }
          variant={"success"}
        />
        <SnackBarA
          open={hasQueryResponded && !!approvePayoutsResponse.error}
          closeSnackbar={() => setGqlResponded(false)}
          message={
            `Approved Payouts Err: ${
              JSON.stringify(option(approvePayoutsResponse).error())
            }`
          }
          variant={"error"}
        />
        <SnackBarA
          open={hasQueryResponded && !!createPayoutsResponse.error}
          closeSnackbar={() => setGqlResponded(false)}
          message={
            `Created Payouts Err: ${
              JSON.stringify(option(createPayoutsResponse).error())
            }`
          }
          variant={"error"}
        />

        <div style={{ height: '6rem' }}/>
      </div>
    </div>
  )
}

interface ReactProps extends WithStyles<typeof styles> {
}

interface MutationData {
  createPayouts: Payout[];
}
interface MutationVar {
  month: number;
  year: number;
  query?: ConnectionQuery;
}

interface MutationData2 {
  approvePayouts: {
    approvedPayouts: Payout[];
    payoutsAlreadyApproved: Payout[];
  }
}
interface MutationVar2 {
  payoutIds: ID[];
  query?: ConnectionQuery;
}



