import gql from "graphql-tag";


export const RefundFragment = gql`
fragment RefundFragment on Refund {
  id
  transactionId
  orderId
  orderItemIds
  storeId
  createdAt
  reason
}
`;


/// Lookup Store via OrderItem
export const OrderItemStoreFragment = gql`
fragment OrderItemStoreFragment on OrderItem {
  id
  orderId
  product {
    name
    store {
      id
      name
      website
    }
  }
}
`;

export const TransactionFragment = gql`
fragment TransactionFragment on Transaction {
  id
  orderId
  subtotal
  taxes
  paymentProcessingFee
  sellerPayment
  platformFee
  currency
  customerId
  createdAt
  paymentMethod {
    id
    email
    last4
    expYear
  }
  paymentProcessor
  paymentMethodId
  paymentIntentId
  chargeId
  details
  refund {
    ...RefundFragment
  }
}
${RefundFragment}
`;

export const PayoutItemFragment = gql`
fragment PayoutItemFragment on PayoutItem {
  id
  payeeId
  payeeType
  amount
  createdAt
  payoutStatus
  currency
  store {
    id
  }
  orderItemId
  # orderItem
  orderItem {
    ...OrderItemStoreFragment
  }
  txnId
  # transaction
  payoutId
  # payout
  ### Get payout emails for payout item
  store {
    id
    name
    user {
      id
      email
      payoutMethod {
        id
        payoutEmail
        payoutType
        payoutProcessor
        payoutProcessorId
      }
    }
  }
}
${OrderItemStoreFragment}
`;


export const PayoutFragment = gql`
fragment PayoutFragment on Payout {
  id
  payeeId
  payeeType
  payoutDate
  createdAt
  startPeriod
  endPeriod
  payoutStatus
  payoutEmail
  approvedByAdmins {
    id
    firstName
    lastName
    email
  }
}
`;